$root-font-size: 62.5% !default;
$base-app-width: 90% !default;
$base-font-color: white !default;
$reverse-font-color: black !default;
$base-font-size: 1.6rem !default;
$base-background-color: #336774 !default;
$darker-background-color: #255359 !default;
$base-font-family: sans-serif !default;
$base-line-height: 2rem !default;
$base-breakline-color: #ffffff23 !default;
$base-progbar-color: #ffffff71 !default;
$load-progbar-color: #a28c37 !default;
$pass-progbar-color: white !default;
$base-white: white !default;
$darker-white: #ffffff71 !default;
$navBar-height: 6rem;
$button-color-paper: #eeeeeeff !default;
$codeFont: Gotham, system-ui, San Francisco, Roboto, Segoe UI, Ubuntu,
  Helvetica Neue, sans-serif;

$grid-max-width: 96rem !default;
$grid-total-columns: 12 !default;
$grid-gutter-size: 2rem !default;
@font-face {
  font-family: "RobotoMono Regular";
  src: url("../../../assets/fonts/RobotoMono-Regular.ttf");
  font-style: normal;
}
@font-face {
  font-family: "Montserrat SemiBold";
  src: url("../../../assets/fonts/Montserrat-SemiBold.ttf");
  font-style: normal;
}
@font-face {
  font-family: "Montserrat Bold";
  src: url("../../../assets/fonts/Montserrat-Bold.ttf");
  font-style: normal;
}

