@import "../../styles/shared";

.main {
  background-image: url("../../../assets/img/codebkgd.jpg");
  background-repeat: no-repeat;
  background-size: 100% 100%;
  color: $base-font-color;
  height: 100vh;
  position: relative;

  .copyright {
    position: absolute;
    left: 0;
    right: 0;
    margin: 2px auto;
    text-align: center;
    font-weight: 100;
    font-size: 1.3rem;
  }

  .main__text {
    position: absolute;
    bottom: 30rem;
    left: 0;
    right: 0;
    margin: 0 auto;
    h1,
    h2 {
      text-align: center;
    }
    h2 {
      font-weight: 400;
      font-size: 2rem;
    }
  }

  .scroll-indicator {
    border: 2px solid rgba($base-font-color, 0.7);
    border-radius: 20px;
    width: 26px;
    height: 38px;
    position: fixed;
    left: 0;
    right: 0;
    margin: 0 auto;
    bottom: 5rem;
    transition-property: opacity;
    transition-duration: 0.4s;
    transition-timing-function: ease;
    opacity: 0;

    &::before {
      content: "";
      height: 7px;
      width: 2px;
      background: rgba($base-font-color, 0.7);
      border-radius: 4px;
      position: absolute;
      top: 6px;
      left: 50%;
      transform: translateX(-1px);
      animation: intro-scroll-indicator 2s ease infinite;
    }

    @keyframes intro-scroll-indicator {
      0% {
        transform: translate3d(-1px, 0, 0);
        opacity: 0;
      }
      20% {
        transform: translate3d(-1px, 0, 0);
        opacity: 1;
      }
      100% {
        transform: translate3d(-1px, 8px, 0);
        opacity: 0;
      }
    }
  }

  .scroll-indicator--entered {
    opacity: 1;
  }
}
